::-webkit-input-placeholder {
  /* Chrome/Opera/Safari */
  color: #ff0000;
}

::-moz-placeholder {
  /* Firefox 19+ */
  color: #ff0000;
}

:-ms-input-placeholder {
  /* IE 10+ */
  color: #ff0000;
}

:-moz-placeholder {
  /* Firefox 18- */
  color: #ff0000;
}

.visually-hidden {
  width: 1px;
  height: 1px;
  overflow: hidden;
  margin: -1px;
  position: absolute;
  clip: rect(0, 0, 0, 0);
  border: none;
}

.custom-input {
  display: inline-block;
  width: 20px;
  height: 20px;
  background: transparent url(../img/svg/noun_Coffee_848419.svg) no-repeat center;
}

.origin-input:checked + .custom-input {
  background-image: url('../img/svg/noun_Teapot_134361.svg');
}

.origin-input:required + .custom-input {
  background-color: green;
}

.origin-input:disabled + .custom-input {
  background-color: pink;
}